import React from 'react';
import Link from 'gatsby-link';

import { Card } from 'reactstrap';

const SidebarBkp = ({ title, details, blogparam }) => (
  <aside className="col-sm-4 col-md-3 menu-sidebar">
    <div className="panel-group" id="backpageMenu" role="tablist" aria-multiselectable="true"/>
    <div className="panel">
      <div className="panel-heading" role="tab" id="einsteinHeading">
        <h4 className="panel-title">
          <a role="button" data-toggle="collapse" data-parent="#backpageMenu" href="#einsteinCollapse" aria-expanded="true" aria-controls="einsteinCollapse">Einstein<i className="fa fa-chevron-down"></i></a>
        </h4>
      </div>
      <div id="einsteinCollapse" className="panel-collapse collapse in" role="tabpanel" aria-labelledby="einsteinHeading">
        <div className="panel-body">
          <ul>
            <li className="current-menu-item">
              <Link to="/blog">Prediction IO11</Link>
            </li>
            <li>
              <Link to="/blog">Community Portal</Link>
            </li>
          </ul>

        </div>
      </div>
    </div>
    <div className="panel">
      <div className="panel-heading" role="tab" id="einsteinHeading">
        <h4 className="panel-title">
          <a role="button" data-toggle="collapse" data-parent="#backpageMenu" href="#einsteinCollapse" aria-expanded="true" aria-controls="einsteinCollapse">Einstein<i className="fa fa-chevron-down"></i></a>
        </h4>
      </div>
      <div id="einsteinCollapse" className="panel-collapse collapse" role="tabpanel" aria-labelledby="einsteinHeading">
        <div className="panel-body">
          <li>
            <Link to="/blog">Prediction IO</Link>
            <Link to="/blog">Community Portal</Link>
          </li>
        </div>
      </div>
    </div>
  </aside>
)
///blog#sysforge

class Sidebar extends React.Component {
  constructor(props) {
    super(props);
    this.state = { isToggleOn: false };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(props) {
    this.setState(prevState => ({
      isToggleOn: !prevState.isToggleOn
    }));
  }

  render(){
    let sidebarCss = null;
    if (!this.state.isToggleOn ) sidebarCss =  "";
    else sidebarCss =  "current-menu-item";
    return(
      <div className="panel">
        <div className="panel-heading" role="tab" id="einsteinHeading">
          <h4 className="panel-title">
            <div><i className="fa fa-circle-o-notch fa-spin"></i>&nbsp;&nbsp;<u>{this.props.header}</u></div>
          </h4>
        </div>
        <div id="einsteinCollapse"  >
          <div className="panel-body">
            <ul>
              <li className={sidebarCss}>
                <Link to={this.props.url} onClick={this.handleClick}><i className="fa fa-shield fa-rotate-270"></i>&nbsp;{this.props.blogtag}</Link>
              </li>
            </ul>
          </div>
        </div>
      </div>
    )
  }
}

class SidebarFn extends React.Component {
  constructor(props) {
    super(props);
    this.state = { isToggleOn: true };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(props) {
    this.setState(prevState => ({
      isToggleOn: !prevState.isToggleOn
    }));
  }

  render(){
    return(
      <aside className="col-sm-4 col-md-4 menu-sidebar">
        <Card>
        <Sidebar header="Cloud Strategy" blogtag="On Premise to PaaS Model" url="/blog#cloudStrategy"/>
        <Sidebar header="Microservices" blogtag="Use Apache & Free License Library" url="/blog#microservices"/>
        <Sidebar header="Solution Portability" blogtag="Tradeoff: SaaS Vendor & OSS" url="/blog#portability"/>
        <Sidebar header="Data Integration" blogtag="Elegant UI & Data Security" url="/blog#integration"/>
        <Sidebar header="Business Process" blogtag="Manage Workflow & Approval" url="/blog#bpmn"/>
        <Sidebar header="Polyglot SDLC" blogtag="How I Build & Release Portfolio " url="/blog#intro"/>
        </Card>
      </aside>
    )
  }
}

export default SidebarFn;



